/* eslint-disable indent */
import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
  },
  {
    path: '/tos',
    name: 'Tos',
    component: () => import( /* webpackChunkName: "tos" */
    '../views/tos/index.vue')
  },
  {
    path: '/UserLogin',
    name: 'UserLogin',
    component: () => import('../views/UserLogin.vue'),
  },
  {
    path: '/personal/profile',
    name: 'PersonalProfile',
    component: () => import(
      '../views/personal/profile/index.vue'),
  },
  {
    path: '/personal/home',
    name: 'PersonalHome',
    component: () => import(
      '../views/personal/home/index.vue'),
  },
  {
    path: '/Personal',
    name: 'Personal',
    component: () => import('../views/personal/Personal.vue'),
  },
  {
    path: '/signOut',
    name: 'signOut',
    component: () => import('../views/personal/signOut.vue'),
  },
  {
    path: '/sumbitProposal',
    name: 'sumbitProposal',
    component: () => import('../views/personal/sumbitProposal.vue'),
  },
  {
    path: '/TwLogin',
    name: 'TuanweiLogin',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "tuanwei-login" */
      '../views/TwLogin.vue'),
  },
  {
    path: '/tuanwei/home',
    name: 'TuanweiHome',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "tuanwei-home" */
      '../views/tuanwei/home/index.vue'),
  },
  {
    path: '/tuanwei/profile',
    name: 'TuanweiProfile',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "tuanwei-profile" */
      '../views/tuanwei/profile/index.vue'),
  },
  {
    path: '/tuanwei/apply',
    name: 'TuanweiApply',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "tuanwei-apply" */
      '../views/tuanwei/apply/index.vue'),
  },
  {
    path: '/tuanwei/proposal',
    name: 'TuanweiProposal',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "tuanwei-proposal" */
      '../views/tuanwei/proposal/index.vue'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
